<template>
  <div class="home">
    <div class="nav">
      <van-nav-bar>
        <template #left>
          <div class="nav_left">
            <div class="sayhello">下午好！</div>
            <div class="who">{{ userInfo.nickName }}</div>
          </div>
        </template>
        <template #right>
          <div class="nav_right">
            <van-search placeholder="请输入搜索关键词" @input="search" />

            <div class="search-con" v-if="isSearch">
              <div
                class="search-list"
                v-for="(item, index) in searchData"
                :key="index"
              >
                <div @click="goDetail(item.pid)">{{ item.name }}</div>
              </div>
            </div>
          </div>
        </template>
      </van-nav-bar>
    </div>

    <!-- 轮播图 -->

    <div class="banner">
      <div class="banner-box">
        <van-swipe @change="changeCurrentIndex" :autoplay="5000" loop>
          <van-swipe-item v-for="(item, index) in bannerData" :key="index">
            <img
              class="auto-img"
              :src="item.bannerImg"
              alt=""
              @click="goDetail(item.pid)"
            />
          </van-swipe-item>
          <template #indicator>
            <div class="index-box">
              <div
                :class="{ active: index == currentIndex }"
                v-for="(item, index) in bannerData"
                :key="index"
              ></div>
            </div>
          </template>
        </van-swipe>
      </div>
      <div class="product clearfix">
        <div class="left fl">
          <van-sidebar v-model="activeKey">
            <van-sidebar-item
              :title="item.title"
              v-for="(item, index) in productTitle"
              :key="index"
              :class="activeKey == index ? 'active' : ''"
              @click="toggleMenu(item, index, item.type)"
            >
            </van-sidebar-item>
          </van-sidebar>
        </div>

        <div class="right fl">
          <div
            class="item clearfix"
            v-for="(item, index) in productList"
            :key="index"
            @click="goDetail(item.pid)"
          >
            <div class="item-left fl">
              <img :src="item.smallImg" alt="" class="auto-img" />
            </div>
            <div class="item-right fl">
              <div class="name">{{ item.name }}</div>
              <div class="info">
                <div class="price">￥{{ item.price }}</div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>

    
  </div>
</template>

<script>
import "../assets/less/home.less";
export default {
  name: "Home",
  data() {
    return {
      //当前轮播图片索引
      currentIndex: 0,

      //轮播数据
      bannerData: [],

      //热卖商品数据
      hotProduct: [],

      isSearch: false,
      // 侧边导航栏点亮的下标
      activeKey: 0,
      productTitle: [{ title: "咖啡" }, { title: "拿铁" }, { title: "瑞纳冰" }],
      productList: [],
      userInfo: [],

      searchData: [],
    };
  },

  created() {
    //获取轮播图数据
    this.getBannerData();

    this.getTypes();

    this.getUserInfo();
  },

  methods: {
    //修改轮播图片索引
    changeCurrentIndex(index) {
      // console.log("index ==> ", index);
      this.currentIndex = index;
    },

    //获取轮播图数据
    getBannerData() {
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
      });

      //发起注册请求
      this.axios({
        method: "GET",
        url: "/banner",
        params: {
          appkey: this.appkey,
        },
      })
        .then((result) => {
          this.$toast.clear();

          if (result.data.code == 300) {
            this.bannerData = result.data.result;
          }
        })
        .catch((err) => {
          this.$toast.clear();

          console.log("err ==> ", err);
        });
    },

  
    productListData(item) {
      this.productList = [];

      for (let i = 0; i < this.hotProduct.length; i++) {
        if (this.hotProduct[i].name.indexOf(item.title) > -1) {
          this.productList.push(this.hotProduct[i]);
          console.log(this.productList);
        }
      }
    },
    //切换菜单商品
    toggleMenu(item, index, type) {
      if (this.activeKey == index) {
        return;
      }

      this.activeKey = index;
      this.getProductByType(type);
    },

    //获取商品类型
    getTypes() {
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
      });

      this.axios({
        method: "GET",
        url: "/type",
        params: {
          appkey: this.appkey,
        },
      })
        .then((result) => {
          this.$toast.clear();

          if (result.data.code == 400) {
            let data = result.data.result;
            this.productTitle.map((v) => {
              for (let i = 0; i < data.length; i++) {
                if (v.title == data[i].typeDesc) {
                  v.type = data[i].type;
                  break;
                }
              }
            });

            let type = this.productTitle[this.activeKey].type;

            this.getProductByType(type);
          }

        })
        .catch((err) => {
          this.$toast.clear();
          console.log("err ==> ", err);
        });
    },

    //根据类型获取商品
    getProductByType(type) {
      let params = {
        appkey: this.appkey,
      };
      params.key = "type";
      params.value = type;

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
      });

      this.axios({
        method: "GET",
        url: "/typeProducts",
        params,
      })
        .then((result) => {
          this.$toast.clear();

          if (result.data.code == 500) {
            this.productList = result.data.result;
          }
        })
        .catch((err) => {
          this.$toast.clear();
          console.log("err ==> ", err);
        });
    },

    //查看商品详情
    goDetail(pid) {
      this.$router.push({ name: "Detail", params: { pid } });
    },
    //切换菜单商品
    toggleMenu(item, index, type) {
      if (this.activeKey == index) {
        return;
      }

      this.activeKey = index;

      this.getProductByType(type);
    },

    //获取用户信息
    getUserInfo() {
      let tokenString = localStorage.getItem("tokenString");

      if (!tokenString) {
        return;
      }

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
      });

      this.axios({
        method: "GET",
        url: "/findMy",
        params: {
          appkey: this.appkey,
          tokenString,
        },
      })
        .then((result) => {
          this.$toast.clear();

          if (result.data.code == "A001") {
            this.userInfo = result.data.result[0];
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },
    // 商品搜索
    search(value) {
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
      });
      this.axios({
        method: "GET",
        url: "/search",

        params: {
          appkey: this.appkey,
          name: value,
        },
      })
        .then((res) => {
          this.$toast.clear();
          if (res.data.code == "Q001") {
            this.isSearch = true;
            this.searchData = res.data.result;
          }
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },
  },
};
</script>

<style lang="less" scoped>
</style>